.contact-popup {
    background-color: #13405b;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    padding: 80px 0 105px;
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &::-webkit-scrollbar {
        width: 0px !important;
    }
    &::-webkit-scrollbar-track {
        background-color: $color-sec !important;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #12405a !important;
    }

    scrollbar-width: thin !important;
    scrollbar-color: rgba(#12405a, 1) rgba($color-sec, 1) !important;

    &.open {
        visibility: visible;
        opacity: 1;

        .col-lg-5,
        .col-lg-6 {
            filter: none !important;
            transform: none !important;
            opacity: 1 !important;
            transition: 1s 0.3s;
        }
    }

    .circles {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .circles li {
        position: absolute;
        display: block;
        list-style: none;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        animation: animate 25s linear infinite;
        bottom: -150px;
    }

    .circles li:nth-child(1) {
        left: 25%;
        width: 80px;
        height: 80px;
        animation-delay: 0s;
    }

    .circles li:nth-child(2) {
        left: 10%;
        width: 20px;
        height: 20px;
        animation-delay: 2s;
        animation-duration: 12s;
    }

    .circles li:nth-child(3) {
        left: 70%;
        width: 20px;
        height: 20px;
        animation-delay: 4s;
    }

    .circles li:nth-child(4) {
        left: 40%;
        width: 60px;
        height: 60px;
        animation-delay: 0s;
        animation-duration: 18s;
    }

    .circles li:nth-child(5) {
        left: 65%;
        width: 20px;
        height: 20px;
        animation-delay: 0s;
    }

    .circles li:nth-child(6) {
        left: 75%;
        width: 110px;
        height: 110px;
        animation-delay: 3s;
    }

    .circles li:nth-child(7) {
        left: 35%;
        width: 150px;
        height: 150px;
        animation-delay: 7s;
    }

    .circles li:nth-child(8) {
        left: 50%;
        width: 25px;
        height: 25px;
        animation-delay: 15s;
        animation-duration: 45s;
    }

    .circles li:nth-child(9) {
        left: 20%;
        width: 15px;
        height: 15px;
        animation-delay: 2s;
        animation-duration: 35s;
    }

    .circles li:nth-child(10) {
        left: 85%;
        width: 150px;
        height: 150px;
        animation-delay: 0s;
        animation-duration: 11s;
    }

    .head {
        text-align: center;
        h1 {
            font-size: 40px;
            color: #fff;
            margin-bottom: 13px;
            font-family: bold;
            position: relative;

            @include Sm {
                display: flex;
                flex-direction: column-reverse;
            }

            img {
                position: absolute;
                top: 0;
                right: 0;
                cursor: pointer;
                transition: 0.5s;

                @include Sm {
                    position: unset;
                    display: block;
                    margin: auto;
                    margin-bottom: 20px;
                }

                &:hover {
                    transform: rotate(360deg);
                }
            }
        }

        p {
            font-size: 16px;
            font-family: med;
            color: rgba(#fff, 0.6);
            margin-bottom: 100px;

            @include Sm {
                margin-bottom: 30px;
            }
        }
    }

    .row {
        .col-lg-6 {
            form {
                width: 100%;

                .field {
                    position: relative;

                    input {
                        width: 100%;
                        height: 60px;
                        background-color: #fff;
                        border: 0;
                        padding: 0 60px;
                        border-radius: 100px;
                        color: $color-prim;
                        font-size: 18px;
                        font-family: med;
                        margin-bottom: 10px;

                        &::placeholder {
                            color: $color-prim;
                            font-size: 18px;
                            font-family: med;
                            transition: 0.5s;
                        }

                        &:focus {
                            outline: none;
                            box-shadow: none;
                            &::placeholder {
                                opacity: 0.2;
                            }
                        }
                    }

                    textarea {
                        width: 100%;
                        padding: 0 60px;
                        padding-top: 22px;
                        height: 126px;
                        border-radius: 20px;
                        margin-bottom: 26px;
                        color: $color-prim;
                        font-size: 18px;
                        font-family: med;

                        &::placeholder {
                            color: $color-prim;
                            font-size: 18px;
                            font-family: med;
                            transition: 0.5s;
                        }

                        &:focus {
                            outline: none;
                            box-shadow: none;
                            &::placeholder {
                                opacity: 0.2;
                            }
                        }
                    }

                    img {
                        position: absolute;
                        top: 20px;
                        right: 24px;
                    }
                }

                button {
                    @extend .Flex;
                    @extend .hover;
                    width: 160px;
                    height: 60px;
                    background-color: $color-sec;
                    font-size: 18px;
                    font-family: med;
                    color: #fff;
                    border: 0;
                    border-radius: 100px;

                    @include Sm {
                        margin-right: auto;
                        margin-left: auto;
                    }
                }
            }

            > .img {
                img {
                    animation: float 8s linear infinite;

                    @include Sm {
                        display: block;
                        margin: auto;
                        margin-bottom: 20px;
                    }
                }
            }
        }

        .col-12 {
            margin-top: 80px;

            @include Sm {
                margin-top: 50px;
            }

            .contact-Min {
                margin: 0;
                background-color: transparent;

                .content {
                    margin: 0;
                    background-color: transparent;
                    padding: 0;
                    justify-content: center;

                    @include Sm {
                        flex-direction: column;
                    }

                    .cont {
                        &:not(:last-child) {
                            margin-left: 75px;

                            @include Sm {
                                margin-right: 0;
                                margin-left: 0;
                            }
                        }

                        @include Sm {
                            width: 272px !important;
                        }

                        a {
                            color: #fff;

                            span {
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
    }
}
